.product-card{
    padding: 25px 18px;
    box-shadow: 0.5px 0.5px 3px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    grid-gap: clamp(16px, 1.2vw, 18px);
    max-width: 367px;
    background-color: rgba(var(--bg-a),1.0);
    transition: all .4s;

    &:hover{
        box-shadow: 0.5px 0.5px 3px rgba(0, 0, 0, 0.3);
    }

    &__top{
        position: relative;
    }

    &__bottom{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 34px;
    }

    &__descr{
        grid-column:  span 2;
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: rgba(var(--cl-c),1.0);
        max-width: 260px;
    }

    &__price{
        display: flex;
        flex-direction: column;
        grid-gap: 4px;

        &-sale{
            font-family: var(--font-family);
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            text-decoration-line: line-through;
            color:  rgba(var(--cl-f),1.0);
        }

        &-value{
            font-family: var(--font-family);
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            color: rgba(var(--cl-d),1.0);
        }
    }

    &__buy{
        justify-self: end;
    }

    &__sale{
        width: 100px;
        height: 25px;
        background-color: rgba(var(--bg-f),1.0);
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        color: rgba(var(--cl-a),1.0);
        position: absolute;
        top: 0;
        left: -18px;
    }
} 