  position: absolute;
  transform: translate(-50%, -50%);
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay.none {
  display: none !important;
}
.overlay.active {
  position: fixed;
  z-index: 20;
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}
