.custom-icon {
  display: flex;
  width: 20px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  pointer-events: none;
}
.custom-icon--star {
  width: 40px;
  height: 20px;
  background: var(--star-icon);
}
.custom-icon--stars {
  width: 30px;
  height: 30px;
  position: absolute;
  overflow: hidden;
}
.custom-icon--stars::before {
  content: "";
  height: 1000px;
  background: var(--stars-icon);
  background-repeat: repeat;
  position: absolute;
  width: 100%;
  z-index: 1;
  animation: stars 80s linear infinite;
}
.custom-icon--stars::after {
  position: absolute;
  content: "";
  top: -2px;
  z-index: 2;
  width: 100%;
  height: 7px;
  background: var(--gradient-b);
}
.custom-icon--nav {
  background-image: var(--custom-stars-nav);
  width: 92px;
  height: 50px;
}

.main-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--bg-c), 1);
  width: clamp(160px, 19.267vw, 289px);
  height: 50px;
}

.footer-logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-title {
  position: relative;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: clamp(20px, 2vw, 30px);
  line-height: 150%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: rgba(var(--cl-b), 1);
  display: inline;
}
.main-title b {
  background-color: rgba(var(--bg-c), 1);
  height: clamp(8px, 0.8vw, 12px);
  width: clamp(42px, 3.2vw, 48px);
  display: inline-flex;
  position: relative;
  bottom: 4px;
  left: clamp(16px, 1.6vw, 24px);
}

.main-pretitle {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(18px, 2vw, 30px);
  line-height: 100%;
  color: rgba(var(--cl-c), 1);
}

p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: rgba(var(--cl-c), 1);
}

.green {
  color: rgba(var(--cl-d), 1);
}

.main-button {
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 270px;
  height: 51px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: rgba(var(--cl-a), 1);
  background-color: rgba(var(--bg-c), 1);
}

.view-button {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
